import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import Highlighter from 'react-highlight-words';
import {
  Form,
  Tabs,
  Table,
  Popconfirm,
  Tooltip,
  Button,
  Modal,
  Row,
  Col,
  Badge,
  message,
  Select,
  Input,
  Icon,
} from 'antd';
import styled from 'styled-components';
import { injectIntl } from 'react-intl';
import { getEds, addEd, deleteEd, enabledEd, disabledEd } from '../../services';

const { Option } = Select;
const { TabPane } = Tabs;
const FormItem = Form.Item;
const ButtonAction = styled(Button)`
  margin: 5px;
`;

const EdsCmp = props => {
  const {
    form,
    history,
    form: { getFieldDecorator },
    intl: { messages },
    match: {
      params: { filter, id },
    },
  } = props;

  const [visible, setVisible] = useState(false);
  const [mode, setMode] = useState('add');
  const [loading, setLoading] = useState(true);
  const [eds, setEds] = useState([]);
  const [nodes, setNodes] = useState([]);
  const [models, setModels] = useState([]);
  const [searchInput, setSearchInput] = useState('');
  const [searchText, setSearchText] = useState('');

  useEffect(() => {
    fetchEds();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const clearFilter = async () => {
    try {
      setLoading(true);
      const response = await getEds();

      const {
        data: { eds, nodes, models },
      } = response;
      setEds(eds);
      setModels(models);
      setNodes(nodes);
      setLoading(false);
      history.push('/dashboard/eds');
    } catch (error) {
      message.error(error);
    }
  };

  const fetchEds = async () => {
    try {
      let response;
      if (filter === 'node' || filter === 'model') {
        response = await getEds({ filter, id });
      } else {
        response = await getEds();
      }

      const {
        data: { eds, nodes, models },
      } = response;
      setEds(eds);
      setModels(models);
      setNodes(nodes);
      setLoading(false);
    } catch (error) {
      message.error(error);
    }
  };

  const delEd = async ed => {
    try {
      setLoading(true);
      await deleteEd({ id: ed.id });
      message.success(messages.default.success);
      fetchEds();
    } catch (error) {
      message.error(error);
    }
  };

  const enaEd = async node => {
    try {
      setLoading(true);
      await enabledEd({ id: node.id });
      message.success(messages.default.success);
      fetchEds();
    } catch (error) {
      message.error(error.message);
    }
  };
  const disEd = async node => {
    try {
      setLoading(true);
      await disabledEd({ id: node.id });
      message.success(messages.default.success);
      fetchEds();
    } catch (error) {
      message.error(error.message);
    }
  };

  const handleReset = () => {
    form.resetFields();
  };

  const handleSubmit = e => {
    e.preventDefault();

    form.validateFields(async (err, values) => {
      if (!err) {
        const form = {
          nodeid: values.node_id,
          modelid: values.model_id,
          // status: values.status,
        };
        setLoading(true);

        if (mode === 'add') {
          try {
            await addEd(form);
            message.success(messages.default.success);
            handleReset();
            setVisible(false);
            fetchEds();
          } catch (error) {
            setVisible(true);
            setLoading(false);
            message.error(error.message);
          }
        } else {
          // try {
          //   await setModel(form);
          //   message.success(messages.default.success);
          //   handleReset();
          //   setVisibleEdit(false);
          //   fetchModels();
          // } catch (error) {
          //   message.error(error.message);
          // }
        }
      }
    });
  };

  const getColumnSearchProps = (dataIndex, service = false) => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            setSearchInput(node);
            // this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => handleSearch(selectedKeys, confirm)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => handleSearch(selectedKeys, confirm)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button onClick={() => handleSearchReset(clearFilters)} size="small" style={{ width: 90 }}>
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),

    onFilter: (value, record) => {
      const el = nodes.filter(item => {
        return item.id === record.node_id;
      });
      if (el.length) {
        return el[0].name
          .toString()
          .toLowerCase()
          .includes(value.toLowerCase());
      }
    },

    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput.select());
      }
    },
    render: text => (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[searchText]}
        autoEscape
        textToHighlight={text.toString()}
      />
    ),
  });

  const handleSearch = (selectedKeys, confirm) => {
    confirm();
    setSearchText(selectedKeys[0]);
  };

  const handleSearchReset = clearFilters => {
    clearFilters();
    setSearchText('');
  };

  const columns = [
    {
      title: 'Id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: messages.eds.node_id,
      dataIndex: 'node_id',
      key: 'node_id',
      ...getColumnSearchProps('node_id'),
      render: text => {
        const el = nodes.filter(item => {
          return item.id === text;
        });
        if (el.length) {
          return <span>{el[0].name}</span>;
        } else {
          return (
            <span>
              <strong>
                <Tooltip placement="top" title={'The Node does not exist'}>
                  <Badge status="error" text={text} />
                </Tooltip>
              </strong>
            </span>
          );
        }
      },
    },
    {
      title: messages.eds.model_id,
      dataIndex: 'model_id',
      key: 'model_id',
      render: text => {
        const el = models.filter(item => {
          return item.id === text;
        });
        if (el.length) {
          return <span>{el[0].name}</span>;
        } else {
          return (
            <span>
              <strong>
                <Tooltip placement="top" title={'The Model does not exist'}>
                  <Badge status="error" text={text} />
                </Tooltip>
              </strong>
            </span>
          );
        }
      },
    },
    {
      title: messages.eds.engine_id,
      dataIndex: 'engine_id',
      key: 'engine_id',
    },
    {
      title: messages.eds.running_mode,
      dataIndex: 'running_mode',
      key: 'running_mode',
    },
    {
      title: messages.eds.last_tu,
      dataIndex: 'last_tu',
      key: 'last_tu',
    },
    {
      title: messages.eds.status,
      dataIndex: 'status',
      key: 'status',
      render: (text, record) => {
        const status = [
          'STATUS_READY',
          'STATUS_RUNNING',
          'STATUS_STARTING',
          'STATUS_STOPPING',
          'STATUS_BUSY',
          'STATUS_OFF',
          'STATUS_ERROR',
        ];

        if (text === -1) return status[6];
        return status[text];
      },
    },
    {
      title: messages.default.actions,
      key: 'action',
      width: 150,

      render: (text, record) => (
        <span>
          {record.status === 5 ? (
            <Tooltip placement="top" title={messages.eds.enabled}>
              <ButtonAction
                onClick={() => {
                  enaEd(record);
                }}
                ghost
                size="small"
                type="danger"
                shape="circle"
                icon="stop"
              />
            </Tooltip>
          ) : (
            <Tooltip placement="top" title={messages.eds.disabled}>
              <ButtonAction
                onClick={() => {
                  disEd(record);
                }}
                ghost
                size="small"
                type="primary"
                shape="circle"
                icon="check"
              />
            </Tooltip>
          )}

          <Popconfirm
            onConfirm={() => {
              delEd(record);
            }}
            title={messages.eds.confirmDelete}
            okText={messages.default.yes}
            cancelText={messages.default.no}
          >
            <Tooltip placement="top" title={messages.eds.delete}>
              <ButtonAction size="small" type="danger" shape="circle" icon="delete" />
            </Tooltip>
          </Popconfirm>
        </span>
      ),
    },
  ];

  const operations = (
    <React.Fragment>
      {filter && (
        <Button
          loading={loading}
          onClick={clearFilter}
          style={{ marginRight: 20 }}
          size="small"
          type="dashed"
          icon="close"
        >
          {filter === 'node' ? `Node (${id})` : `Model (${id})`}
        </Button>
      )}

      <Button
        loading={loading}
        onClick={() => {
          setVisible(true);
          setMode('add');
        }}
        type="primary"
        icon="apartment"
      >
        {messages.eds.add}
      </Button>
    </React.Fragment>
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  return (
    <div>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        <Tabs defaultActiveKey="all" tabBarExtraContent={operations}>
          <TabPane tab={messages.eds.all} key="all">
            <Table
              size="small"
              loading={loading}
              columns={columns}
              dataSource={eds}
              rowKey={record => record.id}
            />
          </TabPane>
        </Tabs>

        <Modal
          centered
          width={600}
          title={messages.eds.add}
          visible={visible}
          onCancel={() => {
            setVisible(false);
          }}
          footer={[
            <Button
              key="back"
              onClick={() => {
                setVisible(false);
              }}
            >
              {messages.default.cancel}
            </Button>,
            <Button key="submit" type="primary" loading={loading} onClick={handleSubmit}>
              {messages.default.create}
            </Button>,
          ]}
        >
          <Form {...formItemLayout}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label={messages.eds.node_id} hasFeedback>
                  {getFieldDecorator('node_id', {
                    rules: [
                      {
                        required: true,
                        message: messages.eds.rules.node_id.required,
                      },
                    ],
                  })(
                    <Select>
                      {nodes.map(item => (
                        <Option key={item.id} value={item.id}>
                          <strong>({item.id}) </strong>
                          {item.name}
                        </Option>
                      ))}
                    </Select>
                  )}
                </FormItem>
                <FormItem label={messages.eds.model_id} hasFeedback>
                  {getFieldDecorator('model_id', {
                    rules: [
                      {
                        required: true,
                        message: messages.eds.rules.model_id.required,
                      },
                    ],
                  })(
                    <Select>
                      {models.map(item => (
                        <Option key={item.id} value={item.id}>
                          <strong>({item.id}) </strong>
                          {item.name}
                        </Option>
                      ))}
                    </Select>
                  )}
                </FormItem>
                {/* <FormItem label={messages.eds.status} hasFeedback>
                  {getFieldDecorator('status', {
                    rules: [
                      {
                        required: true,
                        message: messages.eds.rules.status.required,
                      },
                    ],
                  })(
                    <Select>
                      <Option value={0}>STATUS_READY</Option>
                      <Option value={1}>STATUS_RUNNING</Option>
                      <Option value={2}>STATUS_STARTING</Option>
                      <Option value={3}>STATUS_STOPPING</Option>
                      <Option value={4}>STATUS_BUSY</Option>
                      <Option value={5}>STATUS_OFF</Option>
                      <Option value={-1}>STATUS_ERROR</Option>
                    </Select>
                  )}
                </FormItem> */}
              </Col>
            </Row>
          </Form>
        </Modal>
      </div>
    </div>
  );
};
const WrappedEdsCmp = Form.create()(injectIntl(EdsCmp));

export default withRouter(WrappedEdsCmp);
